.container {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 200px;
    width: 600px;
    background: #fff;
    padding-bottom: 30px;
  }
  
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
  }
  
  .text {
    color: #009d0d;
    font-size: 48px;
    font-weight: 700;
  }
  .underline {
    width: 61px;
    height: 6px;
    background: #009d0d ;
    border-radius: 9px;
  }
  
  .inputs {
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
  }
  
  .input {
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 80px;
    background: #eaeaea;
    border-radius: 6px;
  }
  
  .input img {
    margin: 0px 30px;
  }
  
  .input input {
    height: 50px;
    width: 400px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 19px;
  }
  
  .remember-me {
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    gap: 10px;
    color: #797979;
    font-size: 16px;
  }
  
  .forgot-password {
    padding-left: 62px;
    margin-top: 27px;
    color: #797979;
    font-size: 18px;
    position: relative;
  }
  .forgot-password span {
    color: #3c009d;
    cursor: pointer;
  }
  
  .submit-container {
    display: flex;
    gap: 30px;
    margin: 60px auto;
  }
  
  .submit {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #fff;
    background: #00b439;
    border-radius: 50px;
    font-size: 19px;
    cursor: pointer;
  }
  .gray {
    background: #eaeaea;
    color: #676767;
  }

  .continue-container {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }
  
  .continue {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #fff;
    background: #4c00b4;
    border-radius: 50px;
    font-size: 19px;
    cursor: pointer;
  }

  .error {
    color: red;
    margin-left: 60px;
    font-size: 20px;
  }